class book_apis {

    async get_book_by_isbn(isbn) {
        //http://jeolpan.godohosting.com/jeolpan/app/phps/book/get_book_by_isbn.php

        console.log("got isbn: " + isbn);
        console.log("got isbn: " + isbn);

        var json_data = null;

        var map_data = new FormData();
        map_data.append( "isbn", isbn);   

        await fetch("http://jeolpan.godohosting.com/jeolpan/app/phps/book/get_book_by_isbn.php", {
            method: "POST",
            headers: {
               // "Content-Type": "application",
            },
            body: map_data,
        }).then((res) => res.json()).then(data => json_data = data)

        try{
            console.log(json_data);
            console.log(json_data[0]["idx"]);
        }catch(error){
            console.log(error);
        }

        return json_data;
    }

}

const outs = { book_apis }

export default outs